import { Api } from '../base/api';
export class ApiModel extends Api {
    constructor(cdn, serviceBaseUrl, requestOptions) {
        super(serviceBaseUrl, requestOptions);
        this.cdn = cdn;
    }
    makeApiCall(endpoint, method, payload) {
        const options = {
            method,
            headers: { 'Content-Type': 'application/json' },
            body: payload ? JSON.stringify(payload) : undefined,
        };
        return fetch(`${this.baseUrl}${endpoint}`, options).then((response) => {
            if (!response.ok) {
                throw new Error(`API call failed with status ${response.status}: ${response.statusText}`);
            }
            return response.json();
        });
    }
    async fetchProductDetails(productId) {
        const product = await this.makeApiCall(`/product/${productId}`, 'GET');
        return Object.assign(Object.assign({}, product), { image: `${this.cdn}${product.image}` });
    }
    async fetchAllProducts() {
        const response = await this.makeApiCall(`/product`, 'GET');
        return response.items.map((product) => (Object.assign(Object.assign({}, product), { image: `${this.cdn}${product.image}` })));
    }
    async placeOrder(orderData) {
        return this.makeApiCall(`/order`, 'POST', orderData);
    }
}
