import { Component } from '../base/component';
import { ensureElement } from '../../types/fns';
export class Page extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.basketCounterElement = ensureElement('.header__basket-counter');
        this.galleryElement = ensureElement('.gallery');
        this.mainWrapper = ensureElement('.page__wrapper');
        this.headerBasketElement = ensureElement('.header__basket');
        this.headerBasketElement.addEventListener('click', () => {
            this.events.emit('basket:show');
        });
        this.initEventListeners();
    }
    initEventListeners() {
        this.headerBasketElement.addEventListener('click', this.handleBasketClick.bind(this));
    }
    handleBasketClick() {
        this.events.emit('basket:show');
    }
    set counter(value) {
        this.updateText(this.basketCounterElement, String(value));
    }
    set catalog(items) {
        this.galleryElement.replaceChildren(...items);
    }
    set locked(value) {
        this.mainWrapper.classList.toggle('page__wrapper_locked', value);
    }
}
