import { BaseForm } from './baseForm';
export class ContactForm extends BaseForm {
    constructor(formElement, events) {
        super(formElement, events);
        this.emailInput = formElement.elements.namedItem('email');
        this.phoneInput = formElement.elements.namedItem('phone');
    }
    set email(value) {
        this.emailInput.value = value;
    }
    set phone(value) {
        this.phoneInput.value = value;
    }
}
