export class Component {
    constructor(container) {
        this.container = container;
    }
    updateClass(element, className, add) {
        if (add) {
            element.classList.add(className);
        }
        else {
            element.classList.remove(className);
        }
    }
    updateText(target, content) {
        if (target) {
            target.textContent = String(content);
        }
    }
    setElementState(target, isDisabled) {
        if (target) {
            isDisabled
                ? target.setAttribute('disabled', 'true')
                : target.removeAttribute('disabled');
        }
    }
    hideElement(target) {
        target.style.display = 'none';
    }
    showElement(target) {
        target.style.display = '';
    }
    updateImage(imageElement, source, alternativeText) {
        if (imageElement) {
            imageElement.src = source;
            if (alternativeText) {
                imageElement.alt = alternativeText;
            }
        }
    }
    render(properties) {
        if (properties) {
            Object.assign(this, properties);
        }
        return this.container;
    }
}
