import { Component } from '../base/component';
import { ensureElement } from '../../types/fns';
export class Modal extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.closeButton = ensureElement('.modal__close', container);
        this.contentElement = ensureElement('.modal__content', container);
        this.attachEventListeners();
    }
    attachEventListeners() {
        this.closeButton.addEventListener('click', this.close.bind(this));
        this.container.addEventListener('click', this.close.bind(this));
        this.contentElement.addEventListener('click', (event) => event.stopPropagation());
        document.addEventListener('keydown', this.handleKeyDown.bind(this));
    }
    handleKeyDown(event) {
        if (event.key === 'Escape' || event.key === 'Esc') {
            this.close();
        }
    }
    set content(value) {
        if (value === null) {
            this.contentElement.innerHTML = '';
        }
        else {
            this.contentElement.replaceChildren(value);
        }
    }
    open() {
        this.container.classList.add('modal_active');
        this.events.emit('modal:opened');
    }
    close() {
        this.container.classList.remove('modal_active');
        this.content = null;
        this.events.emit('modal:closed');
    }
    render(data) {
        super.render(data);
        this.open();
        return this.container;
    }
}
